import React from 'react';

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Container from '../../../components/Container'


import bg from './bg.svg'

const Page = ({ resizeRatio }) => {
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container position="relative">
        <Box position="absolute" left="0" right="0" top="0">
          <Image data-src={bg} />
        </Box>
        <Box
          position="absolute"
          style={{
            width: 511 * resizeRatio,
            left: 223 * resizeRatio,
            top: 151 * resizeRatio,
          }}
        >
          <Text.h2 color="primary">觀眾支持行為剖析<Text.inline fontSize="1.25rem" color="white">（年平均）</Text.inline></Text.h2>
        </Box>
        <Box
          position="absolute"
          textAlign="center"
          lineHeight="1.25"
          color="white"
          style={{ left: 197 * resizeRatio, top: 423 * resizeRatio }}
        >
          <Text.h2 fontSize="4.5rem" color="primary">53%</Text.h2>
          <Text.h4 fontSize="1.5rem">購票張數</Text.h4>
          <Text.h4 fontSize="1.5rem">超過<Text.inline fontSize="2rem" color="white">2</Text.inline>張</Text.h4>
        </Box>
        <Box
          position="absolute"
          textAlign="center"
          lineHeight="1.25"
          color="white"
          style={{ left: 503 * resizeRatio, top: 400 * resizeRatio }}
        >
          <Text.h2 fontSize="4.5rem" color="primary">35%</Text.h2>
          <Text.h4 fontSize="1.5rem">會員購票</Text.h4>
          <Text.h4 fontSize="1.5rem">超過<Text.inline fontSize="2rem" color="white">1</Text.inline>張</Text.h4>
        </Box>
        <Box
          position="absolute"
          textAlign="center"
          lineHeight="1.25"
          color="white"
          style={{ left: 800 * resizeRatio, top: 405 * resizeRatio }}
        >
          <Text.h2 fontSize="4.5rem" color="primary">88%</Text.h2>
          <Text.h4 fontSize="1.5rem">偏好欣賞單一<br />的演出類型</Text.h4>
          <Text fontSize="1em" mt="0.5em">（如：只買戲劇或音樂）</Text>
        </Box>
      </Container>
    </Box>
  );
};

export default Page;
