import React from 'react';

import frame from './frame.svg'
import top from './top.svg'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import DotsBg from '../../../components/DotsBg'


const Page = ({ resizeRatio }) => {
  return (
    <DotsBg height="100vh" position="relative">
      <Box position="absolute" top={86 * resizeRatio} bottom="0" left="0" right="0">
        <Image src={frame} />
        <Box textAlign="center" color="white" position="absolute" top={154 * resizeRatio} left="50%" transform="translateX(-50%)" whiteSpace="nowrap">
          <Text.h2 color="yellow" fontSize="3.75rem">當表演走向各地的觀眾</Text.h2>
          <Text.h4 fontSize="2rem">各地區的消費數據觀察</Text.h4>
        </Box>
        <Box textAlign="center" width={600 * resizeRatio} color="white" position="absolute" top={424 * resizeRatio} left="50%" transform="translateX(-50%)">
          <Text fontSize="1.375rem">2016-2018年陸續開幕的屏東演藝廳、台中歌劇院、高雄衛武營，捲動了全台藝文表演的欣賞人流，明顯地改變表演藝術各地區的市場規模與消費習慣。</Text>
          <Text mt="1.75em">註：各地區消費數據以各節目演出地點之場館地址判定</Text>
        </Box>
      </Box>
      <Box position="absolute" top="0" bottom="0" left="0" right="0">
        <Image src={top} />
      </Box>
    </DotsBg>
  );
};

export default Page;
