import React from 'react';

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Button from '../../../components/Button'
import DotsBg from '../../../components/DotsBg'
import Container from '../../../components/Container';

import ShareButton from './ShareButton'
import bglogo from './bglogo.svg'
import chair from './chair.svg'

const Page = ({ resizeRatio }) => {
  return (
    <DotsBg height="100vh">
      <Container pt="2.5em" position="relative" height="100%">
        <Box position="absolute" left="0" right="0" top="0.5em">
          <Image src={bglogo} />
        </Box>
        <Box position="absolute" style={{ top: 190 * resizeRatio }} left="50%" transform="translateX(-50%)">
          <Text.h2 fontSize="2.25rem">THANK YOU</Text.h2>
        </Box>
        <Box position="absolute" top={347 * resizeRatio} left="50%" transform="translateX(-50%)" whiteSpace="nowrap">
          <Text.h2 color="yellow">讓更多人遇見表演藝術的美好</Text.h2>
        </Box>
        <Box textAlign="center" color="white" fontSize="1.5rem" position="absolute" style={{ top: 413 * resizeRatio }} left="50%" transform="translateX(-50%)">
          <Text>透過數據持續關注產業的發展動向</Text>
          <Text>推動表演在全台各地持續開花</Text>
        </Box>
        <Box whiteSpace="pre-wrap" color="white" position="absolute" top={556 * resizeRatio} left="50%" transform="translateX(-50%)">
          <Button.dark px="2.5rem" py="1.375rem" textAlign="left" lineHeight="1.5" onClick={() => {}}>{`下載更完整的數據洞察\n《2013-2019年兩廳院售票系統消費行為報告`}</Button.dark>
          <Flex mt="1rem" fontSize="1.5em" justifycontent="center" alignItems="center">
            <Box>與朋友分享此資訊</Box>
            <ShareButton />
          </Flex>
        </Box>
        <Box position="absolute" left="0" right="0" bottom="-8em">
          <Image src={chair} />
        </Box>
      </Container>
    </DotsBg>
  );
};

export default Page;
