import React from 'react';
import { SizeMe } from 'react-sizeme'
import { format } from 'd3-format'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Container from '../../../components/Container'
import DotsBg from '../../../components/DotsBg'
import RoundTitleLabel from '../../../components/RoundTitleLabel'
import LineChart from '../../../components/Charts/LineChart'
import { schemas } from '../../../components/ThemeProvider/theme'

import useCSV from '../../../utils/useCSV'

import bg from './bg.svg'
import coin from './coin.svg'
import ticket from './ticket.svg'
import frame from './frame.svg'

import csv from './p9.csv'

const Page = ({ containerWidth, resizeRatio }) => {
  const { data } = useCSV(csv)
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container position="relative" height="100%">
        <Image data-src={bg} />
        <Box top="3em" left="11.75em" position="absolute">
          <Text.h2 color="primary">決定購票的價位</Text.h2>
        </Box>
        <Box top={717 * resizeRatio} left={250 * resizeRatio} position="absolute">
          <Text color="white">註1：中位數係指將所有數值排序後，位於50%處的數值，相較於平均數，中位數較不受極端值影響。</Text>
        </Box>
        <DotsBg position="absolute" width={914 * resizeRatio} top={207 * resizeRatio} left={280 * resizeRatio}>
          <Image data-src={frame} />
          <Box position="absolute" top="0" left="2.75em" right="2.75em" bottom="0">
            <RoundTitleLabel mt="2.5em">2013-2019年 各年齡購買票價中位數</RoundTitleLabel>
            <SizeMe monitorHeight>
              {({ size: { width, height } }) => (
                <Box position="absolute" top="8.5em" left="0" right="0" bottom="1.5em">
                  {data && (
                    <LineChart
                      width={width}
                      height={height}
                      data={data}
                      xAccessor={d => d['年齡']}
                      keys={Object.keys(schemas.show)}
                      xTick={{ label: '（歲）' }}
                      yTick={{
                        label: '購買票價中位數（元）',
                        tickValues: [0, 225, 450, 675, 900],
                      }}
                      margin={{
                        top: 1,
                        left: 7,
                        right: 5,
                      }}
                      colors={Object.values(schemas.show)}
                    />
                  )}
                </Box>
              )}
            </SizeMe>
          </Box>
        </DotsBg>
        <Box top={511 * resizeRatio} left={82 * resizeRatio} width={282 * resizeRatio} position="absolute">
          <Image data-src={ticket} />
          <Box position="absolute" top="2.5em" left="3em" right="3.75em">
            <Text.h3 fontSize="2.125em">表演類型</Text.h3>
            <Text.h3 fontSize="1.625em" lineHeight="1.375">是票價差異的主要原因</Text.h3>
          </Box>
        </Box>
      </Container>
      <Box top={-126 * resizeRatio} right={-48 * resizeRatio} width={355 * resizeRatio} position="absolute">
        <Image data-src={coin} />
      </Box>
    </Box>
  );
};

export default Page;
