import React from 'react'

import Text from './Text'

const NumberAndText = ({ children, is, inlineIs, ...props }) => {
  let parsed = children
  if (typeof children === 'string') {
    const re = /[^\d|\-|.|%]+/
    const m = re.exec(children)
    if (m) {
      parsed = []
      if (m.index > 0) {
        parsed.push(children.substr(0, m.index))
      }
      parsed.push(<Text.inline key={`t-${m.index}`} is={inlineIs || 'span'} fontSize="0.875em">{m[0]}</Text.inline>)
      parsed.push(children.substr(m.index + m[0].length))
    }
  }

  return (
    <Text is={is} fontWeight="bold" {...props}>
      {parsed}
    </Text>
  )
}

export default NumberAndText
