import React from 'react';
import { SizeMe } from 'react-sizeme'
import { format } from 'd3-format'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import DotsBg from '../../../components/DotsBg'
import Container from '../../../components/Container'
import RoundTitleLabel from '../../../components/RoundTitleLabel'
import StackedBars from '../../../components/Charts/StackedBars'
import FullRight from '../../FullRight'
import FullLeft from '../../FullLeft'

import { schemas } from '../../../components/ThemeProvider/theme'

import csv from './p8.csv'
import useCSV from '../../../utils/useCSV'

import right from './right.svg'
import left from './left.svg'
import bg from './bg.svg'

const Page = ({ resizeRatio }) => {
  const { data } = useCSV(csv)
  return (
    <Box height="100%" bg="orange" position="relative">
      <Container position="relative">
        <Box position="absolute" top="0" left="0" right="0">
          <Image src={bg} />
        </Box>
        <Box position="absolute" top={60 * resizeRatio} left={194 * resizeRatio}>
          <Text.h2 color="primary">找到想看的節目類型</Text.h2>
        </Box>
      </Container>
      <FullRight src={right}>
        <DotsBg position="absolute" right={60 * resizeRatio} top={242 * resizeRatio} width={960 * resizeRatio} height={460 * resizeRatio} pointerEvents="all">
          <RoundTitleLabel mt="2.375em" ml="3em">2013-2019年 各年齡層購票張數佔比</RoundTitleLabel>
          <SizeMe monitorHeight>
            {({ size: { width, height } }) => (
              <Box position="absolute" top="6.75em" left="2.375em" right="1em" bottom="1.5em">
                {data && (
                  <StackedBars
                    width={width}
                    height={height}
                    data={data}
                    xAccessor={d => d['歲']}
                    keys={Object.keys(schemas.show)}
                    xTick={{ label: '（歲）' }}
                    yTick={{
                      label: '購票張數佔比（％）',
                      tickFormat: d => d * 100,
                      tickValues: [0, 0.2, 0.4, 0.6, 0.8, 1],
                    }}
                    valueFormat={format('.0%')}
                    tooltipFormat={format('.0%')}
                    margin={{
                      top: 1,
                      bottom: 2.25,
                    }}
                    colors={Object.values(schemas.show)}
                  />
                )}
              </Box>
            )}
          </SizeMe>
        </DotsBg>
      </FullRight>
      <FullLeft src={left}>
        <Box position="absolute" top={260 * resizeRatio} left={188 * resizeRatio} width={172 * resizeRatio} pointerEvents="all">
          <Box>
            <Text fontSize="2.875em" lineHeight="1">20-34<Text.inline fontSize="1.75rem">歲</Text.inline></Text>
            <Text fontSize="1.25em">偏好欣賞</Text>
            <Text fontSize="1.75em" fontWeight="bold">戲劇節目</Text>
          </Box>
          <Box mt="0.875em">
            <Text fontSize="2.875em" lineHeight="1">35-44<Text.inline fontSize="1.75rem">歲</Text.inline></Text>
            <Text fontSize="1.75em" fontWeight="bold">親子節目</Text>
            <Text fontSize="1.25em">的主要客群</Text>
          </Box>
          <Box mt="1.375em">
            <Text fontSize="2.875em" lineHeight="1">40<Text.inline fontSize="1.75rem">歲以後</Text.inline></Text>
            <Text fontSize="1.75em" fontWeight="bold">欣賞音樂演出</Text>
            <Text fontSize="1.25em">的比例上升</Text>
          </Box>
        </Box>
      </FullLeft>
    </Box>
  );
};

export default Page;
