import React from 'react';
import { SizeMe } from 'react-sizeme'
import { format } from 'd3-format'

import frame from './frame.svg'
import light from './light.svg'
import csv from './p4.csv'

import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import DotsBg from '../../../components/DotsBg'
import RoundTitleLabel from '../../../components/RoundTitleLabel'
import StackedBars from '../../../components/Charts/StackedBars'

import useCSV from '../../../utils/useCSV'

const Page = ({ resizeRatio }) => {
  const { data } = useCSV(csv)
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container pt="2em" position="relative" height="100%">
        <Box textAlign="center">
          <Text.h2 fontWeight="bold" color="primary" fontSize="2.75em">
            支持表演藝術的觀眾大幅成長
          </Text.h2>
          <Text.h4 color="white" fontSize="1.5em">
            2019年兩廳院售票系統的購票會員人數成長28%
          </Text.h4>
          <Box position="absolute" width={77 * resizeRatio} left={138 * resizeRatio} top={55 * resizeRatio}>
            <Image src={light} />
          </Box>
        </Box>
        <DotsBg position="absolute" top={140 * resizeRatio} left="0" right="0">
          <Image data-src={frame} />
          <Box position="absolute" top="0" left="10.5em" right="10.5em" bottom="5em">
            <RoundTitleLabel mt="3.75em">2013-2019年 購票會員人數</RoundTitleLabel>
            <SizeMe monitorHeight>
              {({ size: { width, height } }) => (
                <Box position="absolute" top="10em" left="0" right="0" bottom="3.375em">
                  {data && (
                    <StackedBars
                      width={width}
                      height={height}
                      data={data}
                      xAccessor="年"
                      keys={['購票人數']}
                      xTick={{ label: '（年）' }}
                      yTick={{
                        label: '人數（萬）',
                        tickValues: [0, 5, 10, 15, 20],
                      }}
                      valueFormat={format('.1f')}
                      margin={{
                        top: 1.5,
                      }}
                      colors={[ '#9faac4' ]}
                      hideLegned
                      noTooltip
                    />
                  )}
                </Box>
              )}
            </SizeMe>
          </Box>
        </DotsBg>
        <Box position="absolute" style={{ top: 720 * resizeRatio, left: 276 * resizeRatio }} color="white">
          <Text>註1：會員其在該年度曾購票1張以上，即視為在該年度曾購票。</Text>
          <Text>註2：會員登記地區-其他: 會員填寫居住地址為無法辨識者。</Text>
        </Box>
      </Container>
    </Box>
  );
};

export default Page;
