import React, { useState } from 'react';
import { SizeMe } from 'react-sizeme'
import { format } from 'd3-format'

import box from './p3-box.csv'
import count from './p3-count.csv'
import event from './p3-event.csv'

import Carousel from '../../../components/Carousel'
import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Text from '../../../components/Text'

import DotsBg from '../../../components/DotsBg'
import RoundTitleLabel from '../../../components/RoundTitleLabel'
import StackedBars from '../../../components/Charts/StackedBars'
import { schemas } from '../../../components/ThemeProvider/theme'

import useCSV from '../../../utils/useCSV'

import years from '../../years'

const handleData = d => d && years.map(year => d.reduce((yd, dd) => {
  yd[dd['類別']] = dd[year]
  return yd
}, { 年: year }))

const Page = ({ resizeRatio }) => {
  const { data: boxData } = useCSV(box)
  const { data: countData } = useCSV(count)
  const { data: eventData } = useCSV(event)
  const data = [
    { name: '節目票房', axisLabel: '金額（億）', data: boxData },
    { name: '演出場次', axisLabel: '場次（場）', data: eventData },
    { name: '售票張數', axisLabel: '張數（萬張）', data: countData },
  ].map(d => ({ ...d, data: handleData(d.data) }))
  const [activeIndex, setActiveIndex] = useState(0)
  const prevIndex = activeIndex === 0 ? data.length - 1 : activeIndex - 1
  const nextIndex = activeIndex === data.length - 1 ? 0 : activeIndex + 1

  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container pt="2em" position="relative" height="100%">
        <Box textAlign="center">
          <Text.h2 fontWeight="bold" color="primary" fontSize="2.75em">
            靠近一點 觀察市場變化
          </Text.h2>
          <Text.h4 color="white" fontSize="1.5em">
            戲劇與音樂節目票房 呈現明顯上升趨勢
          </Text.h4>
        </Box>
        <DotsBg
          position="absolute"
          width={1160 * resizeRatio}
          top={168 * resizeRatio}
          left={104 * resizeRatio}
          height={512 * resizeRatio}
          border="1.5px solid black"
        >
          {[0, 1].map(i => (
            <Box
              key={i}
              position="absolute"
              top="-1.5px"
              left={i ? -1.5 : 'auto'}
              right={i ? 'auto' : -1.5}
              width="5.25em"
              bottom="-1.5px"
              border="1.5px solid black"
              bg="#8e452d"
            />
          ))}
          <SizeMe monitorHeight>
            {({ size: { width, height } }) => (
              <Box position="absolute" top="0" left="5.25em" right="5.25em" bottom="2em">
                <Carousel
                  config={{ afterChange: setActiveIndex }}
                  prevLabel={data[prevIndex].name}
                  nextLabel={data[nextIndex].name}
                  arrowsOffset="5.5em"
                >
                  {data.map(d => (
                    <Box key={`chart-${d.name}`} pt="2.5em" px="2em">
                      <RoundTitleLabel>2013-2019年 {d.name}</RoundTitleLabel>
                      {d.data && (
                        <StackedBars
                          width={width - 32 * resizeRatio}
                          height={height - 90 * resizeRatio}
                          data={d.data}
                          xAccessor="年"
                          keys={Object.keys(schemas.showWithOther)}
                          xTick={{ label: '（年）' }}
                          yTick={{
                            label: d.axisLabel,
                            numTicks: 4,
                          }}
                          colors={Object.values(schemas.showWithOther)}
                          valueFormat={format('.0f')}
                          margin={{
                            top: 2.5,
                            left: 6.5,
                            right: 6.5,
                          }}
                          showSum
                        />
                      )}
                    </Box>
                  ))}
                </Carousel>
              </Box>
            )}
          </SizeMe>
        </DotsBg>
        <Box position="absolute" style={{ top: 707 * resizeRatio, left: 244 * resizeRatio }} color="white">
          <Text>註1：2013-2014年戲劇節目因太陽劇團大幅提升票房，視為單一年度特殊現象，後續相關統計分析將予以排除。</Text>
          <Text>註2：其他節目類別為非戲劇、音樂、舞蹈或親子節目，包含講座、工作坊等活動。</Text>
        </Box>
      </Container>
    </Box>
  );
};

export default Page;
