import React from 'react';
import { LinePath, Line, Bar } from '@vx/shape';
import { Group } from '@vx/group';
import { AxisBottom, AxisLeft } from '@vx/axis';
import { scaleBand, scaleLinear, scaleOrdinal } from '@vx/scale';
import { withTooltip, Tooltip } from '@vx/tooltip';
import { LegendOrdinal } from '@vx/legend';
import { localPoint } from '@vx/event';
import mapValues from 'lodash/mapValues'
import { useImmer } from 'use-immer';

import useResponsive from '../../contexts/mediaQuery/useResponsive'
import Box from '../Box'
import Flex from '../Flex'
import Text from '../Text'
import Checkbox from '../Checkbox'
import TickLabel from './TickLabel'

let tooltipTimeout;

const defaultMargin = {
  top: 3,
  left: 5,
  right: 5,
  bottom: 3,
}

export default withTooltip(
  ({
    data,
    xAccessor,
    keys,
    colors,
    width,
    height,
    margin,
    tooltipOpen,
    tooltipLeft,
    tooltipTop,
    tooltipData,
    hideTooltip,
    showTooltip,
    xTick = {},
    yTick = {},
    valueFormat = v => v,
  }) => {
    if (!data || !xAccessor || !keys || !colors) return null
    const { em } = useResponsive()
    if (isNaN(em)) return null
    const [activeKeys, updateActiveKeys] = useImmer(keys.reduce((ak, k) => {
      ak[k] = true
      return ak
    }, {}))
    const onlyOneKey = Object.values(activeKeys).reduce((c, b) => c + b, 0) === 1
    const emedMargin = mapValues(Object.assign({}, defaultMargin, margin), d => d * em)
    // bounds
    const xMax = width - emedMargin.left - emedMargin.right;
    const yMax = height - emedMargin.top - emedMargin.bottom;

    const valueMax = data.reduce((ret, cur) => Math.max(ret,
      keys.reduce((t, k) => Math.max(t, +cur[k]), 0)
    ), 0);

    // scales
    const xScale = scaleBand({
      domain: data.map(xAccessor),
      padding: 0.5,
    });
    const yScale = scaleLinear({
      domain: [0, valueMax],
      nice: true,
    });

    // const color = k => colors[k]
    const color = scaleOrdinal({
      domain: keys,
      range: colors,
    })

    xScale.rangeRound([0, xMax]);
    yScale.range([yMax, 0]);
    const eachBand = xScale.step()
    return (
      <Box position="relative">
        <svg width={width} height={height}>
          <Group top={emedMargin.top} left={emedMargin.left}>
            {keys.map(key => (
              <LinePath
                key={key}
                data={data}
                x={d => xScale(xAccessor(d))}
                y={d => yScale(d[key])}
                stroke={color(key)}
                strokeWidth={6}
                strokeLinecap="round"
                opacity={+activeKeys[key]}
              />
            ))}
          </Group>
          <Bar
            x={emedMargin.left}
            y={0}
            width={xMax + eachBand * 0.5}
            height={height}
            fill="transparent"
            data={data}
            onMouseMove={event => {
              if (tooltipTimeout) clearTimeout(tooltipTimeout);
              const { x, y } = localPoint(event);
              const index = Math.min(Math.round((x - emedMargin.left - eachBand * 0.5) / eachBand), data.length - 1);
              const top = y + (2 * em);
              const left = xScale(xAccessor(data[index])) + emedMargin.left;
              showTooltip({
                tooltipData: { index },
                tooltipTop: top,
                tooltipLeft: left
              });
            }}
            onMouseLeave={event => {
              tooltipTimeout = setTimeout(() => {
                hideTooltip();
              }, 300);
            }}
          />
          <Group left={emedMargin.left}>
            <AxisLeft
              top={emedMargin.top}
              left={0}
              scale={yScale}
              labelProps={{
                fontSize: '1.125em',
                fill: '#fff',
                writingMode: 'tb',
                transform: 'rotate(0)',
                fontStretch: 'condensed',
                fontWeight: 'bold',
                letterSpacing: -8,
                x: -em * 4.25,
                y: 0,
              }}
              stroke="#fff"
              strokeWidth={3}
              tickStroke="#fff"
              tickStrokeWidth={1.5}
              tickLabelProps={(value, index) => ({
                fill: '#fff',
                textAnchor: 'end',
                fontSize: '1.125em',
                dx: '-0.25em',
                dy: '0.25em',
              })}
              tickComponent={({ formattedValue, ...tickProps }) => (
                <TickLabel {...tickProps}>{formattedValue}</TickLabel>
              )}
              {...yTick}
            />
            <AxisBottom
              top={yMax + emedMargin.top}
              strokeWidth={3}
              stroke="#fff"
              labelProps={{
                fill: '#fff',
                fontSize: '1.125em',
                x: xMax,
                // dy: '-0.625em',
                fontWeight: 'bold',
              }}
              scale={xScale}
              hideTicks
              tickLabelProps={() => ({
                fill: '#fff',
                fontSize: '1.25em',
                textAnchor: 'middle',
                dy: '0.25em',
                dx: '-0.5em',
              })}
              tickComponent={({ formattedValue, ...tickProps }) => (
                <TickLabel {...tickProps}>{formattedValue}</TickLabel>
              )}
              {...xTick}
            />
          </Group>
          {tooltipData && (
            <Line
              from={{ x: tooltipLeft, y: emedMargin.top }}
              to={{ x: tooltipLeft, y: yMax + emedMargin.top }}
              stroke="white"
              strokeWidth={2}
              style={{ pointerEvents: 'none' }}
              strokeDasharray="2,2"
            />
          )}
        </svg>
        <Box
          position="absolute"
          top={0}
          right={0}
        >
          <LegendOrdinal scale={color}>
            {labels => (
              <>
                <Text color="white">可依類別點選</Text>
                {labels.map(l => (
                  <Flex
                    key={l.datum}
                    is="label"
                    width={1}
                    my="1em"
                    cursor="pointer"
                  >
                    <Checkbox
                      fontSize="1.5em"
                      color="white"
                      name="active"
                      checked={activeKeys[l.datum]}
                      onChange={(e) => updateActiveKeys(draft => {
                        if (onlyOneKey && !e.target.checked) {
                          draft = mapValues(draft, () => true)
                        } else {
                          draft[l.datum] = e.target.checked;
                        }
                        return draft
                      })}
                    />
                    <Box
                      ml="0.5em"
                      flex="1"
                      bg={l.value}
                      color="black"
                      textAlign="center"
                      borderRadius="2em"
                      px="0.75em"
                      pt="0.25em"
                      userSelect="none"
                    >
                      {l.text}
                    </Box>
                  </Flex>
                ))}
              </>
            )}
          </LegendOrdinal>
        </Box>
        {tooltipOpen && (
          <Tooltip
            top={tooltipTop}
            left={tooltipLeft}
            style={{
              position: 'absolute',
              backgroundColor: 'rgba(0,0,0,0.9)',
              color: 'white'
            }}
          >
            <Box p="0.5em" fontWeight="bold">
              <Text>{xAccessor(data[tooltipData.index])}</Text>
              {keys.map(k => (
                <Text my="0.5em" color={color(k)} key={`tooltip-${k}`}>
                  {k}: {data[tooltipData.index][k]}
                </Text>
              ))}
            </Box>
          </Tooltip>
        )}
      </Box>
    );
  }
);
