import React from 'react';
import { SizeMe } from 'react-sizeme'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Container from '../../../components/Container'
import DotsBg from '../../../components/DotsBg'
import RoundTitleLabel from '../../../components/RoundTitleLabel'
import MultiLine from '../../../components/Charts/MultiLine'

import FullLeft from '../../FullLeft';
import useCSV from '../../../utils/useCSV'

import bg from './bg.svg'
import numbers from './numbers.svg'

import csv from './p11.csv'

const Page = ({ containerWidth, resizeRatio }) => {
  const { data } = useCSV(csv)
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container position="relative" height="100%">
        <Image data-src={bg} />
        <Box top="3.75em" left="6.5em" position="absolute" width="20em" color="white">
          <Text.h2 color="primary">決定購票的價位</Text.h2>
          <Box borderBottom="1px solid white" py="0.5em" mb="0.5em">
            <Text.h3 display="inline" fontSize="2.5em">中午</Text.h3>
            <Text.inline fontSize="1.625em">是購票的尖峰時段</Text.inline>
            <Text fontSize="1.625em">因新節目多於中午12:00正式開賣</Text>
          </Box>
          <Text.h3 fontSize="2.5em">20:00後</Text.h3>
          <Text fontSize="1.625em">也是購票高峰，且年齡愈輕有愈晚購票的傾向趨勢</Text>
        </Box>
        <Box top={700 * resizeRatio} left={527 * resizeRatio} position="absolute">
          <Text color="white">註：購票張數佔比：各年齡會員於各時間之購票張數，佔該年齡會員之總購票張數比例。</Text>
        </Box>
        <DotsBg
          position="absolute"
          width={825 * resizeRatio}
          top={89 * resizeRatio}
          left={478 * resizeRatio}
          height={593 * resizeRatio}
        >
          <RoundTitleLabel mt="2.5em" ml="2em">2013-2019年 各年齡購票張數佔比</RoundTitleLabel>
          <Box position="absolute" top="0" left="2.75em" right="1.5em" bottom="0">
            <SizeMe monitorHeight>
              {({ size: { width, height } }) => (
                <Box position="absolute" top="7.15em" left="0" right="2em" bottom="2em">
                  {data && (
                    <MultiLine
                      width={width}
                      height={height}
                      data={data}
                      xAccessor="時"
                      keys={Object.keys(data[0]).filter(k => k !== '時')}
                      xTick={{ label: '（時）' }}
                      yTick={{
                        label: '購票張數佔比（％）',
                        tickValues: [0, 5, 10, 15],
                      }}
                      valueFormat={d => `${d}%`}
                    />
                  )}
                </Box>
              )}
            </SizeMe>
          </Box>
        </DotsBg>
      </Container>
      <FullLeft src={numbers} />
    </Box>
  );
};

export default Page;
