import React, { useState } from 'react';
import { LinePath, Bar, Line } from '@vx/shape';
import { Group } from '@vx/group';
import { AxisBottom, AxisLeft } from '@vx/axis';
import { scaleBand, scaleLinear } from '@vx/scale';
import { withTooltip, Tooltip } from '@vx/tooltip';
import { localPoint } from '@vx/event';
import mapValues from 'lodash/mapValues'

import useResponsive from '../../contexts/mediaQuery/useResponsive'
import Text from '../Text'
import Box from '../Box'
import Flex from '../Flex'
import NumberAndText from '../NumberAndText'
import TickLabel from './TickLabel'
import theme from '../ThemeProvider/theme'

const TheLinePath = (props) => (
  <LinePath
    stroke="#4d4d4d"
    strokeWidth={3}
    strokeLinecap="round"
    {...props}
  />
)

let tooltipTimeout;
export default withTooltip(({
  data,
  xAccessor,
  keys,
  width,
  height,
  margin = {
    top: 6.75,
    left: 5,
    right: 4,
    bottom: 3,
  },
  tooltipOpen,
  tooltipLeft,
  tooltipTop,
  tooltipData,
  hideTooltip,
  showTooltip,
  xTick = {},
  yTick = {},
  valueFormat = v => v,
}) => {
  if (!data || !xAccessor || !keys) return null
  const x = typeof xAccessor === 'string' ? d => d[xAccessor] : xAccessor
  const { em } = useResponsive()
  if (isNaN(em)) return null
  const [activeKey, updateActiveKey] = useState(keys[0])
  const emedMargin = mapValues(margin, d => d * em)
  // bounds
  const xMax = width - emedMargin.left - emedMargin.right;
  const yMax = height - emedMargin.top - emedMargin.bottom;

  // scales
  const xScale = scaleBand({
    domain: data.map(x),
    padding: 0.5,
  });
  const yScale = scaleLinear({
    domain: [0, 15],
    nice: true,
  });

  xScale.rangeRound([0, xMax]);
  yScale.range([yMax, 0]);
  const eachBand = xScale.step()

  return (
    <Box position="relative">
      <svg width={width} height={height}>
        <Group top={emedMargin.top} left={emedMargin.left}>
          {keys.map(key => key === activeKey ? null : (
            <TheLinePath
              key={key}
              data={data}
              x={d => xScale(x(d))}
              y={d => yScale(d[key])}
            />
          ))}
          <TheLinePath
            data={data}
            x={d => xScale(x(d))}
            y={d => yScale(d[activeKey])}
            stroke={theme.colors.primary}
          />
        </Group>
        <Bar
          x={emedMargin.left}
          y={0}
          width={xMax + eachBand * 0.5}
          height={height}
          fill="transparent"
          data={data}
          onMouseMove={event => {
            if (tooltipTimeout) clearTimeout(tooltipTimeout);
            const p = localPoint(event);
            const index = Math.min(Math.round((p.x - emedMargin.left - eachBand * 0.5) / eachBand), data.length - 1);
            const top = p.y + (2 * em);
            const left = emedMargin.left + xScale(x(data[index]))
            showTooltip({
              tooltipData: { index },
              tooltipTop: top,
              tooltipLeft: left
            });
          }}
          onMouseLeave={event => {
            tooltipTimeout = setTimeout(() => {
              hideTooltip();
            }, 300);
          }}
        />
        <Group left={emedMargin.left}>
          <AxisLeft
            top={emedMargin.top}
            left={0}
            scale={yScale}
            labelProps={{
              fontSize: '1.125em',
              fill: '#fff',
              writingMode: 'tb',
              transform: 'rotate(0)',
              fontStretch: 'condensed',
              fontWeight: 'bold',
              letterSpacing: -8,
              x: -em * 4.25,
              y: 0,
            }}
            stroke="#fff"
            strokeWidth={3}
            tickStroke="#fff"
            tickStrokeWidth={1.5}
            tickLabelProps={(value, index) => ({
              fill: '#fff',
              textAnchor: 'end',
              fontSize: '1.125em',
              dx: '-0.25em',
              dy: '0.25em',
            })}
            tickComponent={({ formattedValue, ...tickProps }) => (
              <TickLabel {...tickProps}>{formattedValue}</TickLabel>
            )}
            {...yTick}
          />
          <AxisBottom
            top={yMax + emedMargin.top}
            strokeWidth={3}
            stroke="#fff"
            labelProps={{
              fill: '#fff',
              fontSize: '1.125em',
              x: xMax,
              // dy: '-0.625em',
              fontWeight: 'bold',
            }}
            scale={xScale}
            hideTicks
            tickLabelProps={() => ({
              fill: '#fff',
              fontSize: '1.25em',
              textAnchor: 'middle',
              dy: '0.25em',
              dx: '-0.25em',
            })}
            tickComponent={({ formattedValue, ...tickProps }) => (
              <TickLabel {...tickProps}>{formattedValue}</TickLabel>
            )}
            {...xTick}
          />
        </Group>
        {tooltipData && (
          <Line
            from={{ x: tooltipLeft, y: emedMargin.top }}
            to={{ x: tooltipLeft, y: yMax + emedMargin.top }}
            stroke="white"
            strokeWidth={2}
            style={{ pointerEvents: 'none' }}
            strokeDasharray="2,2"
          />
        )}
      </svg>
      <Flex
        position="absolute"
        top="0"
        left="0"
        right="0"
        flexWrap="wrap"
        mx="-0.25em"
      >
        {keys.map(key => (
          <Box
            key={key}
            display="inlione-block"
            is="label"
            width="5em"
            textAlign="center"
            my="0.25em"
            mx="0.25em"
            borderRadius="3em"
            fontSize="1.25em"
            pt="0.125em"
            onClick={() => updateActiveKey(key)}
            bg={key === activeKey ? 'primary' : 'white'}
          >
            <NumberAndText>
              {key}
            </NumberAndText>
          </Box>
        ))}
      </Flex>
      {tooltipOpen && (
        <Tooltip
          top={tooltipTop}
          left={tooltipLeft}
          style={{
              position: 'absolute',
              backgroundColor: 'rgba(0,0,0,0.9)',
            color: 'white'
          }}
        >
          <Box p="0.5em" fontWeight="bold">
            <Text color="primary">{x(data[tooltipData.index])}{typeof xAccessor === 'string' && xAccessor}</Text>
            <Text my="0.5em">
              {valueFormat(data[tooltipData.index][activeKey])}
            </Text>
          </Box>
        </Tooltip>
      )}
    </Box>
  );
})
