import React, { Component } from 'react'
import { FaTwitter } from 'react-icons/fa';

import Box from '../../../components/Box';
import Flex from '../../../components/Flex';
import FB from '../../../components/FB';
import Line from '../../../components/Line';
import Link from '../../../components/Link';
import Button from '../../../components/Button';
import { mobileOrDesktop } from '../../../components/ThemeProvider/theme';

const icons = [
  {
    Icon: FB,
    is: Link,
    href: '',
  },
  {
    Icon: Line,
    is: Link,
    href: 'http://line.naver.jp/R/msg/text/?分享給好友看看吧！'},
  {
    Icon: FaTwitter,
    is: Link,
    href: ''
  }
]

class ShareButton extends Component  {
  render() {
    return (
      <Flex alignItems="center" px="0.5em">
        {icons.map(({ Icon, is, href }, index) => (
          <Box ml={index && '0.5em'} key={index}>
            <Button.circle
              color="white"
              bg="black"
              px={index === icons.length - 1 && '0.375em'}
              pt={index === icons.length - 1 && '0.375em'}
              pb={index === icons.length - 1 && '0.375em'}
              is={is}
              href={href}
            >
              <Icon size={index === icons.length - 1 && '1.25em'} />
            </Button.circle>
          </Box>
        ))}
      </Flex>
    )
  }
}

export default ShareButton
