import React from 'react';

import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import FullRight from '../../FullRight'

import right from './right.svg'
import stage from './stage.svg'
import ticket from './ticket.svg'

const Page = ({ resizeRatio }) => {
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Box
        position="absolute"
        top={0}
        bottom={0}
        left={224 * resizeRatio}
        borderLeft="0.125em solid black"
      />
      <Box
        position="absolute"
        top={0}
        bottom={0}
        right={224 * resizeRatio}
        borderLeft="0.125em solid black"
      />
      <Box position="absolute" width={434.39 * resizeRatio} left={144 * resizeRatio} top={313.44 * resizeRatio}>
        <Image src={ticket} />
        <Box position="absolute" textAlign="center" lineHeight="1" width={202 * resizeRatio} left={99.05 * resizeRatio} top={25.45 * resizeRatio}>
          <Box fontSize="6em">197<Text.inline fontSize="2rem">萬</Text.inline></Box>
          <Box fontSize="2em">售票張數</Box>
        </Box>
      </Box>
      <FullRight src={right}>
        <Box position="absolute" textAlign="center" lineHeight="1"  width={243.45 * resizeRatio} right={175.4 * resizeRatio} top={317 * resizeRatio}>
          <Box fontSize="6em">13.6<Text.inline fontSize="2rem">億</Text.inline></Box>
          <Box fontSize="2em">節目票房</Box>
        </Box>
      </FullRight>
      <Container pt="4.375rem" position="relative" height="100%">
        <Box.inline ml="3.5em" fontWeight="bold" borderTop="11px solid" borderColor="coffee" bg="black" py="1.5rem" px="2rem" color="yellow">
          <Text.h2>表演藝術市場的格局展現</Text.h2>
        </Box.inline>
        <Box position="absolute" width={558.41 * resizeRatio} left={475.525 * resizeRatio} top={424 * resizeRatio}>
          <Image src={stage} />
          <Box position="absolute" color="white" textAlign="center" left={106.78 * resizeRatio} top={108.67 * resizeRatio} >
            <Box fontSize="8em" lineHeight="1" color="yellow">7,119<Text.inline fontSize="2rem">場</Text.inline></Box>
            <Box fontSize="2em">演出場次</Box>
            <Box fontSize="1.25em">（平均每天超過19場演出）</Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default Page;
