import React from 'react'
import { MdCheckBox, MdCheckBoxOutlineBlank } from 'react-icons/md'
import styled from 'styled-components'

import Box from './Box'

const Input = styled.input`
opacity: 0;
position: absolute;
visibility: hidden;
`

const Checkbox = ({
  checked,
  name,
  onChange = () => {},
  ...props
}) => {
  return (
    <Box.inline verticalAlign="middle" lineHeight="1" {...props}>
      <Input
        type="checkbox"
        name={name}
        checked={checked}
        onChange={onChange}
      />
      {checked ? <MdCheckBox /> : <MdCheckBoxOutlineBlank />}
    </Box.inline>
  )
}

export default Checkbox
